body.show__modal .backdrop {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
}

body.show__modal .modal {
    transform: scale(1);
}

.backdrop {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 999;

    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition: opacity 200ms ease-in-out, visibility 200ms ease-in-out;

    @media screen and (min-width: 768px) {
        padding-left: 32px;
        padding-right: 32px;
        padding-top: 0px;
        padding-bottom: 0px;
    }
}

.modal {
    padding-top: 8px;
    padding-right: 8px;
    overflow-y: auto;
    overflow-x: auto;
    background-color: #fff;
    transform: scale(1.1);
    transition: transform 200ms ease-in-out;
    @media screen and (min-width: 480px) {
        width: 440px;
    }
    @media screen and (min-width: 768px) {
        width: 704px;
    }
    @media screen and (min-width: 1024px) {
        width: 814px;
    }

}

.modal__wrapper {
    padding-top: 0px;
    padding-left: 16px;
    padding-right: 8px;
    padding-bottom: 40px;

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
        padding-left: 36px;
        padding-right: 72px;
        padding-bottom: 40px;
        }

    @media screen and (min-width: 1024px) {
        padding-left: 8px;
        padding-right: 24px;
    }
}

.close__modal {
    display: block;
    border: 0px;
    width: 30px;
    height: 30px;
    margin-left: auto;
    margin-right: 0px;
    margin-bottom: 10px;
    background-color: #FF6B01;

    @media screen and (min-width: 768px) {
        margin-bottom: 2px;
    }
    @media screen and (min-width: 1024px) {
        margin-right: 8px;
    }
}

// .close-btn:hover {
    
// }

.modal__image {
    display: block;
    min-width: 240px;
    min-height: 357px;
    background-color: teal;
    margin-bottom: 20px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @media screen and (min-width: 768px) {
        margin: 0;
        min-width: 264px;
        min-height: 374px;
    }
    @media screen and (min-width: 1024px) {
        margin: 0;
        min-width: 375px;
        min-height: 478px;
    }

}

.modal__about {
    @media screen and (min-width: 768px) {
        padding-left: 68px;
    }
    @media screen and (min-width: 1024px) {
        padding-left: 16px;
    }
}

.modal__title {
    padding-bottom: 20px;

    font-family: 'Roboto';
    font-weight: 500;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 1.2;
    color: #000000;

    @media screen and (min-width: 1024px) {
        font-size: 30px;
        line-height: 1.17;
    }
}

.modal__lists{
    display: flex;
    padding-bottom: 20px;

}

