.list__value {
    margin-left: 40px;

    @media screen and (min-width: 1024px) {
        margin-left: 85px;
        }
}

.key__item, 
.item__value {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;
    list-style-type: none;

    color: #8C8C8C;
}

.key__item:not(:last-child),
.item__value:not(:last-child) {
    padding-bottom: 8px;
}

.item__value {
    color: #000000;
}

.vote {
    background-color: #FF6B01;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 4px;
    color: #FFFFFF;
 }


.text__title {
    margin: 0;
    padding-bottom: 8px;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;
    text-transform: uppercase;
}

.modal__text {
    padding-bottom: 20px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.7;
    color: #000000;
}
.modal__buttons {
    display: flex;
    justify-content: space-around;
    
    @media screen and (min-width: 1024px) {
        justify-content: flex-start;    
    }
}

.modal__button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 6px;
    width: 100%;
    height: 44px;
    border-radius: 4px;
    border: 1px solid #000000;
    background-color: inherit;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;
    text-transform: uppercase;
    color: #000000;
    cursor: pointer;

    @media screen and (min-width: 1024px) {
        width: 136px;
    }
}

