.modal__button:not(:last-child) {
    margin-right: 20px;

    @media screen and (min-width: 768px) {
        margin-right: 15px;
    }

    @media screen and (min-width: 1024px) {
        margin-left: 8px;
    }
}

.watched__btn {
    background-color: #FF6B01;
    border-color: #FF6B01;
    color: #FFFFFF;
}